import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/store'
import './registerServiceWorker';

import './assets/style/border.css';  //解决移动端一像素边框
import './assets/style/reset.css';  //样式重置

import fastClick from "fastclick";
fastClick.attach(document.body);   //解决延迟问题

import './assets/iconfont/iconfont.css';   //icon图标
import './assets/iconfont/iconfont'

import VueAwesomeSwiper from 'vue-awesome-swiper'; //轮播
import 'swiper/dist/css/swiper.css';
Vue.use(VueAwesomeSwiper);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
