<template>
  <div ref="wrapper" class="wrapper">
    <div>
      <div>
          <p class="list">当前城市</p>
          <button class="city-2">{{this.$store.state.city}}</button>  <!-- 随着点击的改变名字 -->
      </div>
      <div>
          <p class="list-2">热门城市</p>
          <div class="hot">
            <button v-for="(item,index) of hotCities" :key="index" @click="handleClick" class="city">{{item.name}}</button>
          </div>
      </div>
      <div v-for="(item,index) of cities" :key="index">
        <p class="list-2" :ref="index">{{index}}</p>
        <!-- ABCDE -->
        <div @click="handleClick" class="choice" v-for="(city,inx) of item" :key="inx">{{city.name}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CityList",
  props: ["hotCities", "cities"],
  methods:{
    handleClick(event){  /* 点击之后跳转到主页 */
      this.$store.commit("changeCity",event.target.innerText);
      this.$router.push("/") ;
      this.keyword = ""
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  /* 計算屬性 */
  computed:{
      letter(){
          return this.$store.state.letter
      }
  },
  watch:{
      letter(){
          var element = this.$refs[this.letter][0];
          this.scroll.scrollToElement(element)
      }
  },
};
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height: 1000px;
  overflow: hidden;
  position: relative;
      margin-top: 26.467vw;
}
.list {
  color: #555;
  background-color: #eee;
  padding: 15px;
  // margin-top: 120px;
}
.list-2 {
  color: #555;
  background-color: #eee;
  padding: 15px;
}
.city {
  width: 200px;
  padding: 12px;
  color: #555;
  background: white;
  border: 1px solid #ddd;
  border-radius: 12px;
  margin: 22px;
}
.city-2 {
  width: 200px;
  padding: 12px;
  color: #555;
  background: white;
  border: 1px solid #ddd;
  border-radius: 12px;
  margin: 22px;
}
.hot {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.choice {
  display: flex;
  flex-direction: column;
  padding: 28px;
  border-bottom: 1px solid #ddd;
}
.choice p {
  padding: 12px;
}
</style>
